.navbar{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px 0;
    height: 5%;
}
.navbar a,.navbar p{

    padding: 5px;
    font-size: 20px;
    text-decoration: none;
    color: white;
}
.navbar p{
    text-transform: uppercase;
    color: white;
    font-weight: 600;
    text-decoration: underline dotted;
    overflow:hidden;
white-space: nowrap;
text-overflow: hidden;

}