.no-Player {
  --c:#000;
  width: fit-content;
  font-weight: bold;
  font-family: monospace;
  font-size: 30px;
  color: #0000;
  overflow: hidden;
  text-shadow: 0 0 var(--c),20ch 0 var(--c);
  animation: l6 2s infinite linear; 
}
.no-Player:before {
  content:"No Player Yet..."
}
@keyframes l6 {to{text-shadow:-20ch 0 var(--c),0ch 0 var(--c)}}
  @keyframes l24{
    100%{background-position: left}
  }

